@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(36, 36, 80, 0.649);
}

.currency-converter {
  width: 400px;
  background-color: #fff;
  border-radius: 8px;
  padding: 10px 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 20px;
  padding: 10px 0px;
  margin-bottom: 15px;
  color: rgb(84, 72, 72);
  text-align: center;
  border-bottom: 2px dashed #ccc;
  border-top: 2px dashed #ccc;
}

.box {
  width: 100%;
  height: 200px;
  background-image: url('../public/currency-logo.jpg');
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
}

.input-container {
  margin-bottom: 15px;
}

label {
  display: block;
  margin-bottom: 5px;
  color: #5a4c4c;
  font-weight: 500;
}

input,
select {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 18px;
  outline: none;
}

input:focus,
select:focus {
  border-color: #443b3b;
}

.result {
  margin: 20px 0px;
  padding: 10px;
  border: 2px dotted rgb(90, 84, 84);
  border-radius: 4px;
}

.result p {
  margin: 10px 0;
  color: rgb(77, 66, 66);
  font-weight: bold;
  text-align: center;
}